import { request } from './index';

// 获取登录用户的信息
export const getUserProfile = function () {
  return request({
    url: '/app/v1_0/user/profile',
  });
};

// 修改用户的昵称
export const updateUser = function (data) {
  return request({
    method: 'PATCH',
    url: '/app/v1_0/user/profile',
    data,
  });
};

// 修改用户的头像
export const updatePhoto = function (data) {
  return request({
    method: 'PATCH',
    url: '/app/v1_0/user/photo',
    processData: false, //* 必须写
    contentType: false, //* 必须写
    data,
  });
};
