<template>
  <div class="info_content">
    <van-nav-bar class="topbar-container" title="个人信息">
      <van-icon slot="left" name="arrow-left" @click="$router.back(-1)" />
    </van-nav-bar>
    <!-- 隐藏的input上传 -->
    <input
      type="file"
      accept="image/*"
      ref="file"
      hidden
      multiple
      @change="onFileChange"
    />
    <van-cell
      title="头像"
      is-link
      center
      class="avator"
      @click="$refs.file.click()"
    >
      <van-image width="30" height="30" fit="cover" round :src="user.photo" />
    </van-cell>
    <van-cell
      title="昵称"
      @click="isNameShow = true"
      is-link
      :value="user.name"
    />
    <van-cell
      title="性别"
      @click="isSexShow = true"
      is-link
      :value="user.gender ? `女` : `男`"
    />
    <van-cell
      title="生日"
      @click="isBhdShow = true"
      is-link
      :value="user.birthday"
    />
    <!-- 编辑昵称弹出层 -->
    <van-popup
      class="popup_name"
      v-model="isNameShow"
      position="bottom"
      :style="{ height: '100%' }"
    >
      <update-name
        v-if="isNameShow"
        v-model="user.name"
        @close="isNameShow = false"
      ></update-name>
    </van-popup>
    <!-- 编辑性别弹出层 -->
    <van-popup class="popup_sex" v-model="isSexShow" position="bottom">
      <update-sex
        v-if="isSexShow"
        v-model="user.gender"
        @close="isSexShow = false"
      ></update-sex>
    </van-popup>
    <!-- 编辑生日弹出层 -->
    <van-popup class="popup_sex" v-model="isBhdShow" position="bottom">
      <update-bhd
        v-if="isBhdShow"
        v-model="user.birthday"
        @close="isBhdShow = false"
      ></update-bhd>
    </van-popup>
    <!-- 编辑头像弹出层 -->
    <van-popup class="popup_photo" v-model="isPhotoShow" position="bottom">
      <update-photo
        v-if="isPhotoShow"
        @close="isPhotoShow = false"
        :file="file"
        v-model="user.photo"
      ></update-photo>
    </van-popup>
  </div>
</template>

<script>
import { getUserProfile } from '@/api/profile.js';

export default {
  name: 'UserProfile',
  components: {
    UpdateName: () => import('./components/updateName.vue'),
    UpdateSex: () => import('./components/updateSex.vue'),
    UpdateBhd: () => import('./components/updateBirthday.vue'),
    UpdatePhoto: () => import('./components/updatePhoto.vue'),
  },
  data() {
    return {
      isNameShow: false, // 控制修改昵称弹出层
      isSexShow: false, // 控制修改性别弹出层
      isBhdShow: false, // 控制修改生日弹出层
      isPhotoShow: false, // 控制修改头像弹出层
      user: {}, // 用户信息
      file: null, // 上传的文件对象
    };
  },
  methods: {
    // 获取用户数据
    async loadUserProfile() {
      const { data } = await getUserProfile();
      this.user = { ...data.data };
    },
    onFileChange() {
      this.file = this.$refs.file.files[0];
      this.isPhotoShow = true;
      this.$refs.file.value = '';
    },
  },
  created() {
    this.loadUserProfile();
  },
};
</script>

<style lang="less" scoped>
.avator {
  .van-cell__value {
    height: 30px;
  }
}
.popup_name {
  background-color: #f0f0f0;
}
.popup_photo {
  height: 100%;
  background-color: #000;
}
</style>
